.consent_des {
  margin-bottom: 24px;
}
.consent_des_item {
  display: flex;
  align-items: center;
}

.consent_des_text,
.consent_item_text {
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  color: #3b4256;
}
.consent_des_text {
  margin-left: 10px;
}
.consent_des_title {
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
}
.consent_item,
.consent_warning {
  padding: 12px 10px 16px 18px;
  margin-bottom: 24px;
  height: 80px;
  border-radius: 4px;
}

.consent_item {
  position: relative;
  top: -20px;
  cursor: pointer;
  background: #f5f5f7;
  border: 2px solid #f5f5f7;

  transition: border-color 0.2s linear, background 0.2s linear;

  &:hover {
    border-color: #0061d9;
    background: #fff;
  }

  &--second {
    top: -24px;
  }
}

.consent_warning {
  background: #fff5e6;
}
.consent_warning_icon {
  width: 18px;
  height: 22px;
  margin-right: 10px;
}

.consent_footer {
  position: relative;
  left: 0;
  top: -10px;
  font-size: 14px;
  line-height: 20px;
  color: #3b4256;

  a {
    text-decoration: none;
    color: #0061d9;
  }
}

.consent_wrapper {
  position: relative;
  top: -50px;
}
